    import React, { useEffect } from "react";
    import { Card } from "react-bootstrap";
    import AuthorInfo from "../../analytics-new/content/components/authorInfo";
    import { useDispatch } from "react-redux";
    import {
    baseUrl,
    isAppleSafari,
    showAddAccountAlertNew,
    } from "../../../../utils";
    import { Link } from "react-router-dom";
    import parse from "html-react-parser";
    import SocialactionSkeleton from "./skeleton/socialactionSkeleton";
    import { addUserAccount } from "../../../Authorization/connectSocialAcc";
    import { MediaBaseUrl } from "../../../../actions/api";

    const SocialAction = (props) => {
    const { connectedAccounts, feeds, loader } = props;
    const channel = isAppleSafari() ? window : new BroadcastChannel("connect");
    const dispatch = useDispatch();

    const connectaccount = (item, reconnect) => {
        let refresh = 0;
        if (reconnect) {
        refresh = 1;
        }
        localStorage.setItem("refresh", refresh);
        dispatch(showAddAccountAlertNew(item, "dashboardConnect"));
    };

    const readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId, type } = response;
        if (type == "dashboardConnect")
        addUserAccount(
            networkId,
            data,
            () => this.props.getSocialAccounts(),
            localStorage.getItem("ownerId"),
            0,
            Number(localStorage.getItem("refresh"))
        );
        localStorage.removeItem("refresh");
    };

    useEffect(() => {
        channel.addEventListener("message", readAuthenticationCode);
        return () => {
        channel.removeEventListener("message", readAuthenticationCode);
        };
    }, []);

    return (
        <div className="col-md-6 mb-5">
        <div className="socialAccount-post-section h-100">
            <Card className="p-0 card-custom card-stretch">
            <Card.Header className="p-5">
                <div className="m-0 d-flex align-items-center justify-content-between card-title h5 w-100">
                <h6 class="card-label font-weight-bolder m-0 ">
                    Social Accounts
                </h6>
                {connectedAccounts && Object.keys(connectedAccounts).length ? (
                    <div className="">
                    <Link
                        to={`/transactions#connectedAccounts`}
                        className="text-secondary font-size-base"
                    >
                        View All
                    </Link>
                    </div>
                ) : null}
                </div>
            </Card.Header>
            <Card.Body className="p-5">
                {loader ? (
                <SocialactionSkeleton />
                ) : (feeds && Object.keys(feeds).length) ||
                (connectedAccounts && Object.keys(connectedAccounts).length) ? (
                <>
                    {feeds.map((feeddata, index) => {
                    var btntext = "";
                    if (feeddata.Feed.accountId == "") {
                        btntext = "Connect";
                    } else {
                        btntext = "Reconnect";
                    }

                    return (
                        <div className="mb-3">
                        <div class="mb-4 last-child-mb-0 d-block text-secondary cursor-pointer">
                            <div class="p-5 border rounded">
                            <div
                                className="d-flex align-items-center pt-2 pb-2 users_row justify-content-between"
                                key={index}
                            >
                                <div className="d-flex align-items-center flex-grow-1">
                                <div className="symbol symbol-45 symbol-light me-4 con_user_img ms-1 flex-shrink-0">
                                   { !["fa-twitter"].includes(feeddata.Network.icon) ? <span
                                    style={{
                                        backgroundImage: `url(${baseUrl(
                                        `social-icons-feed/${feeddata.Network.icon}.svg`
                                        )})`,
                                        backgroundSize: "70%",
                                        backgroundPosition: "center",
                                        backgroundRepeat: "no-repeat",
                                    }}
                                    ></span> : 
                                       <span>
                                         <svg id="twitter" xmlns="http://www.w3.org/2000/svg" width="22" height="22" viewBox="0 0 44.518 44.518"><path id="Path_5" data-name="Path 5" d="M0,0H44.518V44.518H0Z" fill="none"></path> <path id="Path_7" data-name="Path 7" d="M19.036,13.554,30.942,0H28.121L17.778,11.766,9.523,0H0L12.486,17.794,0,32.006H2.821L13.737,19.578l8.72,12.429H31.98Zm-3.865,4.4L13.9,16.177,3.838,2.083H8.172L16.3,13.462l1.262,1.773L28.12,30.025H23.786Zm0,0" transform="translate(6.313 6.281)" fill="#0f1419"></path></svg> 
                                    </span>
                                }
                                </div>

                                <div className="d-flex text-left flex-column conn_user_info w-75 flex-grow-1">
                                    <p
                                    title="From Hashtag(#)"
                                    className="text-dark m-0 text-nowrap d-flex align-items-center"
                                    >
                                    <strong className="username_conn text-nowrap overflow-hidden d-block text-ellipsis">
                                        {parse(feeddata.Feed.feedName)}
                                        {feeddata.Feed.socialLink ? (
                                        <Link
                                            to={feeddata.Feed.socialLink}
                                            target="_blank"
                                        >
                                            <i className="fa fa-external-link-alt icon-nm ms-1"></i>
                                        </Link>
                                        ) : null}
                                    </strong>
                                    </p>
                                </div>
                                </div>

                                <div className="conn_status disconnnected text-right flex-shrink-0">
                                {![10, 19, 15, 7, 23, 29].includes(
                                    feeddata.Network.id
                                ) && (
                                    <span
                                    className={`reconnect${feeddata.Feed.id}`}
                                    >
                                    <a
                                        onClick={() =>
                                        connectaccount(
                                            feeddata.Network,
                                            feeddata.Feed.accountId !== ""
                                        )
                                        }
                                        className="resetAction btn btn-outline-primary btn-xs"
                                    >
                                        {" "}
                                        {btntext}
                                    </a>
                                    </span>
                                )}
                                </div>
                            </div>
                            </div>
                        </div>
                        </div>
                    );
                    })}

                    {connectedAccounts && Object.keys(connectedAccounts).length
                    ? connectedAccounts.map((e) => {
                        const getData = e.ConnectedAccounts;
                        return (
                            <div className="mb-3">
                            <div class="mb-4 last-child-mb-0 d-block text-secondary cursor-pointer">
                                <div class="p-5 border rounded">
                                {" "}
                                <div class=" d-flex align-items-center justify-content-between">
                                   <AuthorInfo
                                    isIcon={e.Network.icon}
                                    picture={getData.profilePicture}
                                    authorUsername={e.accountName}
                                    authorName={getData.accountName}
                                    link={""}
                                    networkId={getData.networkId}
                                    />
                                    <div className="">
                                    <h5>
                                        {getData.api == 2 ? (
                                        <div className="conn_status connnected text-right">
                                            <div className="conn_status_wrap font-size-lg m-0">
                                            <p className="font-weight-bolder acc_status m-0">
                                                <span className="conn_icon me-1 ">
                                                <svg
                                                    width="14"
                                                    height="14"
                                                    viewBox="0 0 14 14"
                                                    fill="none"
                                                    xmlns="http://www.w3.org/2000/svg"
                                                >
                                                    <path
                                                    fill-rule="evenodd"
                                                    clip-rule="evenodd"
                                                    d="M6.14055e-06 8.00755C0.18814 7.32675 0.822793 6.55563 1.9199 6.78186C2.78723 7.02731 3.50083 7.78774 4.0703 9.02982C7.26277 4.66659 10.0078 1.66443 13.6325 0.538126C13.9557 0.463669 14.1259 0.481481 13.89 0.744398C10.1443 3.72251 6.7572 8.04156 4.12498 13.4117C4.06359 13.5371 3.84323 13.5277 3.77008 13.3911C2.96558 11.9547 2.39412 10.554 1.58944 9.11781C1.23074 8.37519 0.77644 7.84047 6.14055e-06 8.00755Z"
                                                    fill="url(#paint0_diamond_1883_3)"
                                                    />
                                                    <defs>
                                                    <radialGradient
                                                        id="paint0_diamond_1883_3"
                                                        cx="0"
                                                        cy="0"
                                                        r="1"
                                                        gradientUnits="userSpaceOnUse"
                                                        gradientTransform="translate(3.39231 4.44707) rotate(43.0921) scale(13.5428 24.6143)"
                                                    >
                                                        <stop stop-color="#6AC259" />
                                                        <stop
                                                        offset="0.5393"
                                                        stop-color="#6AC259"
                                                        />
                                                        <stop
                                                        offset="1"
                                                        stop-color="#6AC259"
                                                        />
                                                    </radialGradient>
                                                    </defs>
                                                </svg>
                                                </span>{" "}
                                                Connected
                                            </p>
                                            </div>
                                            <small className="font-size-sm">
                                            {e.Network.name} Profile
                                            </small>
                                        </div>
                                        ) : (
                                        <div className="conn_status disconnnected text-right">
                                            <div className="conn_status_wrap">
                                            {![10, 19, 15, 7, 23, 29].includes(
                                                e.Network.id
                                            ) && (
                                                <a
                                                onClick={() =>
                                                    connectaccount(e.Network, true)
                                                }
                                                className="btn btn-outline-primary openGoogleReconnect btn-xs"
                                                data-action="reconnect"
                                                data-src={``}
                                                >
                                                Reconnect
                                                </a>
                                            )}
                                            </div>
                                            <small>{e.Network.name} Profile</small>
                                        </div>
                                        )}
                                    </h5>
                                    </div>
                                </div>
                                </div>
                            </div>
                            </div>
                        );
                        })
                    : null}
                </>
                ) : (
                <div
                    className="not_connected_err not_connected_err_new flex-center overlay-layer d-flex flex-column flex-center"
                    style={{
                    backgroundImage: `url(${MediaBaseUrl}error/not-connected-new.png)`,
                    }}
                >
                    <div className="not_conne_overlay d-flex flex-column flex-center">
                    <Link
                        to={`/transactions#connectedAccounts`}
                        className="btn btn-primary"
                    >
                        Connect
                    </Link>
                    </div>
                </div>
                )}
            </Card.Body>
            </Card>
        </div>
        </div>
    );
    };

    export default React.memo(SocialAction);
