import React from "react";
const SocialactionSkeleton = () => {
  return (
    <div
      class="scroll scroll-pull"
      data-scroll="true"
      data-wheel-propagation="true"
    >
      {[1, 2, 3, 4].map(() => {
        return (
          <div class="d-flex align-items-center p-2 border rounded mb-3">
            <div class="d-flex align-items-center flex-grow-1">
              <div class="symbol symbol-45 symbol-light me-2 con_user_img">
                <span class="sk_img"></span>
              </div>
              <div class="d-flex text-left flex-column conn_user_info w-25">
                <div class="sk_line sk_line_height_12 mb-2"></div>
                <div class="sk_line sk_line_50 sk_line_height_10 mt-2"></div>
              </div>
            </div>
            <div class="conn_status connnected text-right w-25">
              <div class="sk_line sk_line_height_12"></div>
              <div class="sk_line sk_line_80 sk_line_height_10"></div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default React.memo(SocialactionSkeleton);
