import React, { useState } from "react";
import { getFirstLetterOfString, manageSpace } from "../../../../../utils";
import moment from "moment";
import NetworkIcon from "../../layout/common/networkIcon";

const AuthorInfo = (props) => {
  const [imageError, setImageError] = useState(false);

  const redirectTo = (id, username, link) => {

    if (![7].includes(id) && link && username) {

      let redirectLink = ``

      if (props.link.includes('tiktok')) {
        redirectLink = `/search?q=${manageSpace(username)}`
      } else if (props.link.includes('linkedin')) {
        redirectLink = `/search/results/all/?keywords=${manageSpace(username)}`
      }
      else {
        redirectLink = `/${manageSpace(username)}`
      }
      window.open(`https://${new URL(props.link).hostname}${redirectLink}`)
    }
  }

  return (
    props && props != undefined ?
      <div onClick={() => redirectTo(props.networkId, props.authorUsername, props.link)} className={`d-flex align-items-center ${!props.topperfoming ? 'w-50' : 'me-10'}`}>

        <div className="position-relative">
          <div className="symbol symbol-35 symbol-light me-3 con_user_img cursor-pointer rounded-circle overflow-hidden">
            {imageError ? (
              <div className="symbol-image">{getFirstLetterOfString(props.authorName)}</div>
            ) : (
              <img
                onError={() => setImageError(true)}
                src={props.picture}
                className="img-fluid symbol-image"
                width={"20px"}
                height={"20px"}
              />
            )}
          </div>


          {props.isIcon && !props.nIcon ? (
            <div className="m_social_icon_new card-shadow position-absolute bg-white rounded-circle d-flex align-items-center justify-content-center">
              <NetworkIcon icon={props.isIcon} />
            </div>
          ) : null}
        </div>
        <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
          <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
            {props.authorName}
          </p>
          <p class="m-0 d-flex conn_auther_info">
            <small class="overflow-hidden text-nowrap text-ellipsis post_count me-2 ">
              {props.authorUsername ? `@${props.authorUsername}` : props.authorName}
            </small>
            {props.date ?
              <><small class="overflow-hidden text-nowrap text-ellipsis post_count me-2 ">.</small>
                <small class="overflow-hidden text-nowrap text-ellipsis ">
                  {moment(new Date(props.date * 1000)).fromNow()}
                </small></> : null}
          </p>
        </div>
      </div> : null
  );
};

export default React.memo(AuthorInfo);
